import React from 'react';
import ReactDOM from 'react-dom';

import { ThemeProvider } from '@mui/material/styles';
import { HashRouter, Routes, Route } from 'react-router-dom';

import './index.css';
import App from './App';
import CreateMemory from './pages/memory/Create';
import Memories from './pages/memories/List';
import PrelaunchTheme from './theme';
import ViewMemory from './pages/memory/View';
import { ScrollToTop } from './components/common';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import Authenticate from './hocs/require-authentication';

import './fonts/BadScript-Regular.ttf';

const bucket = process.env.REACT_APP_S3_BUCKET;
const authURL = process.env.REACT_APP_AUTH_URL;
const MemoriesRoute = Authenticate(Memories);
const CreateMemoryRoute = Authenticate(CreateMemory);
const ViewMemoryRoute = Authenticate(ViewMemory);

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={PrelaunchTheme}>
      <HashRouter basename={process.env.PUBLIC_URL}>
        <ScrollToTop />
        <Routes>
          <Route path="/" element={<App authURL={authURL} />} />
          <Route
            path="/memories"
            element={<MemoriesRoute bucket={bucket} authURL={authURL} />}
          />
          <Route
            path="/memory/create"
            element={<CreateMemoryRoute bucket={bucket} authURL={authURL} />}
          />
          <Route
            path="/memory/:memoryId"
            element={<ViewMemoryRoute bucket={bucket} authURL={authURL} />}
          />
          <Route path="*" element={<App authURL={authURL} />} />
        </Routes>
      </HashRouter>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root'),
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.unregister();
