import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';

export default function LoginButton({ authURL }) {
  const abovesm = useMediaQuery((theme) => theme.breakpoints.up('sm'));
  return !abovesm ? (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'fixed',
        top: 'calc(100vh - 150px)',
        left: 20,
        borderRadius: '5px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <a
        href={`https://${authURL}/oauth2/authorize`}
        target="_self"
        style={{ textDecoration: 'none', color: 'white' }}
      >
        <Typography variant="h4" color="primary">Login</Typography>
      </a>
    </Button>
  ) : (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'fixed',
        top: 20,
        right: 170,
        borderRadius: '25px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <a
        href={`https://${authURL}/oauth2/authorize`}
        target="_self"
        style={{ textDecoration: 'none', color: 'white' }}
      >
        <Typography variant="h6" color="primary">Login</Typography>
      </a>
    </Button>

  );
}

LoginButton.propTypes = {
  authURL: PropTypes.string.isRequired,
};
