import * as React from 'react';
import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Skeleton from '@mui/material/Skeleton';
import BasePage from '../base_page';
import GridView from './GridView';
import api from '../Api';
import CreateMemoryItem from './CreateMemoryItem';
import MemoryThumbnail from '../MemoryThumbnail';

import imageLoading from '../../static/images/image-loading.gif';

async function getMemories(
  bucket,
  authURL,
  handleMemoryCallback,
  handleTokenCallback,
  handleThumbnailCallback,
  nextToken = '',
) {
  try {
    const resp = await api.getMemories(bucket, authURL, nextToken);
    console.log(resp);
    if (resp) {
      handleThumbnailCallback(resp);
      const thumbnails = await Promise.all(
        api.getThumbnails(bucket, authURL, resp),
      );
      handleMemoryCallback(thumbnails);
      handleTokenCallback(resp.next_continuation_token || '');
    }
  } catch (error) {
    console.log(error);
  }
}

export default function Memories({ bucket, authURL }) {
  const [nextToken, setNextToken] = useState('');
  const [memoryIDs, setMemoryIDs] = useState([]);
  const [memories, setMemories] = useState([]);
  const [count, setCount] = useState(0);
  const [grid, setGrid] = useState(null);

  const picLoaded = () => {
    console.log('Image loaded', count);
    setCount((cnt) => cnt + 1);
  };
  const handleTokenChange = (newToken) => setNextToken(newToken);

  const addMemories = (details) => {
    setMemories((arr) => arr.concat(details));
  };

  const addMemoryIDs = (ids) => {
    setMemoryIDs((arr) => arr.concat(ids));
  };

  useEffect(() => {
    getMemories(
      bucket,
      authURL,
      addMemories,
      handleTokenChange,
      addMemoryIDs,
      nextToken,
    );
  }, []);

  function GetThumbnailGrid() {
    console.log(memories, nextToken);
    const thumbnailGrid = (
      <BasePage>
        <GridView
          memories={memories}
          nextToken={nextToken}
          handleMoreClick={() => getMemories(
            bucket,
            authURL,
            addMemories,
            handleTokenChange,
            nextToken,
          )}
          handleImageLoad={picLoaded}
        />
      </BasePage>
    );
    setGrid(thumbnailGrid);
  }

  useEffect(() => GetThumbnailGrid(), [memories, nextToken]);

  console.log(memories.length, memoryIDs.length, count);

  return (memories.length === memoryIDs.length) && (count >= memoryIDs.length) ? (
    grid
  ) : (
    <BasePage>
      <Grid
        container
        sx={{ bgcolor: 'button.main', py: 1 }}
        justifyContent="left"
        alignItems="flex-start"
      >
        <CreateMemoryItem />

        {memoryIDs.map((memory) => (
          <Grid item key={memory} lg={3} md={4} xs={6} sx={{ p: 1 }}>
            <MemoryThumbnail
              title={<Skeleton type="rectangular" sx={{ width: '60%' }} />}
              date={<Skeleton type="rectangular" sx={{ width: '50%' }} />}
              thumbnailImage={imageLoading}
              handleClick={() => {}}
            />
          </Grid>
        ))}
      </Grid>

      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </BasePage>
  );
}

Memories.propTypes = {
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
};
