import './App.css';

import PropTypes from 'prop-types';
//  import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import { useState, useEffect } from 'react';
import LoginButton from './components/LoginButton';
import SignupButton from './components/SignupButton';
//  import Background from './components/background';
import Main from './components/main';
import b1 from './static/images/b1.webp';

function App({ authURL }) {
  const urls = [b1];
  //  const urls = [
  //  'https://images.unsplash.com/photo-1565536421951-135eb52b6e5d?ixlib=rb-4.0.3&dl=alexandar-todov-Im8ylpB8SpI-unsplash.jpg&q=80&fm=jpg&crop=entropy&cs=tinysrgb',
  //  //  'https://images.pexels.com/photos/952670/pexels-photo-952670.jpeg',
  //  //  'https://images.unsplash.com/photo-1616884950055-861aeb5eb380?ixlib=rb-4.0.3&dl=prchi-palwe-NLqNQ10ppe0-unsplash.jpg&q=80&fm=jpg&crop=entropy&cs=tinysrgb',
  //  //  'https://images.pexels.com/photos/5959508/pexels-photo-5959508.jpeg?cs=srgb&dl=pexels-sachin-rawat-5959508.jpg&fm=jpg&w=4000&h=6000',
  //  ];
  const [image, setImage] = useState(0);
  useEffect(() => {
    const timer = setTimeout(() => setImage((index) => (index + 1) % urls.length), 5000);
    return () => clearTimeout(timer);
  });
  return (
    <div className="App">
      <Box
        component="img"
        src={urls[image]}
        alt="Background"
        width="100%"
        height="100vh"
        sx={{
          positon: 'absolute',
          top: 0,
          bottom: 0,
          objectFit: 'cover',
        }}
      />
      <LoginButton authURL={authURL} />
      <SignupButton authURL={authURL} />
      <Main authURL={authURL} />
    </div>
  );
}

App.propTypes = {
  authURL: PropTypes.string.isRequired,
};

export default App;
